.TaskManager {
  padding: 40px 0;
}
.Container {
  width: 700px;
  background: white;
  color: black;
  -webkit-box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
  overflow: hidden;
  border-radius: 3px;
}
.Header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -ms-flex-align: center;
  align-items: center;
  padding: 20px;
  background: #333333;
  color: white;
}
.HeaderActionsIcon {
  cursor: pointer;
  width: 16px;
  height: 16px;
}
.Content {
  background: #e2e2e2;
  overflow: hidden;
}
.ContentEmpty {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-align: center;
  align-items: center;
  padding: 20px 0;
}
.ContentEmptyIcon {
  padding: 10px;
}
.ContentEmptyIconSvg {
  width: 64px;
  height: 64px;
}
.ContentEmptyText {
  text-align: center;
  padding: 7px 0;
}



