.Field {
  font-family: 'Arial', sans-serif;
  padding: 10px;
  background: white;
  width: 100%;
  margin: 10px 0;
  height: 40px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid #b3aecd;
  border-radius: 3px;
}
.Textarea {
  height: 100px;
}
.Actions {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
  justify-content: flex-end;
}



