.Container {
  padding: 20px;
  background: white;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  cursor: pointer;
  -webkit-transition: ease 0.3s;
  -o-transition: ease 0.3s;
  transition: ease 0.3s;
}
.Header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
  padding: 10px 0;
}
.HeaderTitle {
  display: block;
}
.HeaderTitleWarning {
  color: #43B581;
}
.HeaderActions {
  display: -ms-flexbox;
  display: flex;
  opacity: 1;
}
.HeaderActionsItem {
  cursor: pointer;
  padding: 0 5px;
}
.HeaderActionsIcon {
  width: 12px;
  height: 12px;
}
.IsOverdue {
  background: #A33F41;
  color: white;
}
.IsDragging {
  opacity: 0.5;
}
.IsOverDrag {
  margin-bottom: 30px;
}
.Meta {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
  justify-content: flex-end;
}
.MetaDeadline {
  padding: 5px;
  color: white;
  background: #43B581;
  border-radius: 3px;
}



