.Button {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 10px;
  background: #7289DA;
  height: 40px;
  border-radius: 3px;
  color: white;
  cursor: pointer;
}
.Button:hover {
  background: #4a67cf;
}
.ButtonBlue {
  background: #7289DA;
}
.ButtonBlue:hover {
  background: #4a67cf;
}
.ButtonGreen {
  background: #43B581;
}
.ButtonGreen:hover {
  background: #359066;
}
.ButtonRed {
  background: #A33F41;
}
.ButtonRed:hover {
  background: #7e3132;
}



