.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.6);
  z-index: 1;
}
.Wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  min-height: 100vh;
  -ms-flex-align: center;
  align-items: center;
}
.Container {
  width: 600px;
  background: white;
  border-radius: 3px;
}
.Content {
  padding: 20px;
  color: black;
}



